*{
    box-sizing: border-box;
    padding: 0;
    margin: 0;
    scroll-behavior: smooth;
    font-family: Arial;
}

body{
      /* ff 3.6+ */
      background:-moz-radial-gradient(circle at 0% 57%, rgba(255, 255, 0, 1) 0%, rgba(0, 188, 212, 1) 0%, rgba(130, 140, 238, 1) 100%);

      /* safari 5.1+,chrome 10+ */
      background:-webkit-radial-gradient(circle at 0% 57%, rgba(255, 255, 0, 1) 0%, rgba(0, 188, 212, 1) 0%, rgba(130, 140, 238, 1) 100%);
  
      /* opera 11.10+ */
      background:-o-radial-gradient(circle at 0% 57%, rgba(255, 255, 0, 1) 0%, rgba(0, 188, 212, 1) 0%, rgba(130, 140, 238, 1) 100%);
  
      /* ie 10+ */
      background:-ms-radial-gradient(circle at 0% 57%, rgba(255, 255, 0, 1) 0%, rgba(0, 188, 212, 1) 0%, rgba(130, 140, 238, 1) 100%);
  
      /* global 92%+ browsers support */
      background:radial-gradient(circle at 0% 57%, rgba(255, 255, 0, 1) 0%, rgba(0, 188, 212, 1) 0%, rgba(130, 140, 238, 1) 100%);
}

a{
    color: unset;
    text-decoration: none;
}

.gradient__bg{

    /* ff 3.6+ */
    background:-moz-linear-gradient(90deg, rgba(238, 130, 238, 1) 0%, rgba(0, 209, 255, 1) 100%); 

    /* safari 5.1+,chrome 10+ */
    background:-webkit-linear-gradient(90deg, rgba(238, 130, 238, 1) 0%, rgba(0, 209, 255, 1) 100%);

    /* opera 11.10+ */
    background:-o-linear-gradient(90deg, rgba(238, 130, 238, 1) 0%, rgba(0, 209, 255, 1) 100%);

    /* ie 6-9 */
    filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#00D1FF', endColorstr='#EE82EE', GradientType=0 );

    /* ie 10+ */
    background:-ms-linear-gradient(90deg, rgba(238, 130, 238, 1) 0%, rgba(0, 209, 255, 1) 100%);

    /* global 94%+ browsers support */
    background:linear-gradient(90deg, rgba(238, 130, 238, 1) 0%, rgba(0, 209, 255, 1) 100%);
    
}

.gradient__text{
     /* ff 3.6+ */
     background:-moz-linear-gradient(90deg, rgba(229, 43, 43, 1) 0%, rgba(31, 17, 206, 1) 100%); 

     /* safari 5.1+,chrome 10+ */
     background:-webkit-linear-gradient(90deg, rgba(229, 43, 43, 1) 0%, rgba(31, 17, 206, 1) 100%);
 
     /* opera 11.10+ */
     background:-o-linear-gradient(90deg, rgba(229, 43, 43, 1) 0%, rgba(31, 17, 206, 1) 100%);
 
     /* ie 6-9 */
     filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#1F11CE', endColorstr='#E52B2B', GradientType=0 );
 
     /* ie 10+ */
     background:-ms-linear-gradient(90deg, rgba(229, 43, 43, 1) 0%, rgba(31, 17, 206, 1) 100%);
 
     /* global 94%+ browsers support */
     background:linear-gradient(90deg, rgba(229, 43, 43, 1) 0%, rgba(31, 17, 206, 1) 100%);
 
    background-clip: text;
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
}

.section__padding{
    padding: 3rem 6rem;
}

.section__margin{
    margin: 3rem 6rem;
}

.scale-up-center {
	-webkit-animation: scale-up-center 0.4s cubic-bezier(0.390, 0.575, 0.565, 1.000) both;
	        animation: scale-up-center 0.4s cubic-bezier(0.390, 0.575, 0.565, 1.000) both;
}

 @-webkit-keyframes scale-up-center {
    0% {
      -webkit-transform: scale(0.5);
              transform: scale(0.5);
    }
    100% {
      -webkit-transform: scale(1);
              transform: scale(1);
    }
  }
  @keyframes scale-up-center {
    0% {
      -webkit-transform: scale(0.5);
              transform: scale(0.5);
    }
    100% {
      -webkit-transform: scale(1);
              transform: scale(1);
    }
  }
  
  @media screen and (max-width: 700px) {
    .section__padding{
        padding: 2rem;
    }
    .section__margin{
        margin: 2rem;
    }
  }

  @media screen and (max-width: 550px) {
    .section__padding{
        padding: 2rem 2rem;
    }
    .section__margin{
        margin: 2rem 2rem;
    }
  }