.panda__whatpanda{
    display: flex;
    flex-direction: column;

    padding: 2rem;
        /* ff 3.6+ */
        /* ff 3.6+ */
    background:-moz-linear-gradient(90deg, rgba(238, 130, 238, 1) 0%, rgba(0, 209, 255, 1) 100%); 

    /* safari 5.1+,chrome 10+ */
    background:-webkit-linear-gradient(90deg, rgba(238, 130, 238, 1) 0%, rgba(0, 209, 255, 1) 100%);

    /* opera 11.10+ */
    background:-o-linear-gradient(90deg, rgba(238, 130, 238, 1) 0%, rgba(0, 209, 255, 1) 100%);

    /* ie 6-9 */
    filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#00D1FF', endColorstr='#EE82EE', GradientType=0 );

    /* ie 10+ */
    background:-ms-linear-gradient(90deg, rgba(238, 130, 238, 1) 0%, rgba(0, 209, 255, 1) 100%);

    /* global 94%+ browsers support */
    background:linear-gradient(90deg, rgba(238, 130, 238, 1) 0%, rgba(0, 209, 255, 1) 100%);    

}

.panda__whatpanda-feature{
    display: flex;
}

.panda__whatpanda-feature .panda__features-container__feature{
    color: white;
    margin: 0;
}

.panda__whatpanda-feature .panda__features-container__feature-text{
    color: white;
    max-width: 700px;
}

.panda__whatpanda-heading{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;

    margin: 4rem 0 2rem;
}

.panda__whatpanda-heading h1{
    font-family: Arial;
    font-weight: 800;
    font-size: 34px;
    line-height: 45px;
    flex: 2;

}
.panda__whatpanda-heading p{
    text-align: center;
    color: red;
    font-weight: 800;
    font-size: 16px;
    line-height: 30px;
    cursor: pointer;
    flex: 1;
}

.panda__whatpanda-container{
    display: flex;
    flex-wrap: wrap;
    flex-direction: row;

    margin-top: 2rem;
}

.panda__whatpanda-container .panda__features-container__feature {
    flex: 1;
    margin: 1rem;
    min-width: 210px;
    display: unset;
    flex-direction: column;
    color: white;
}

/* Customizing feature component as per needs */
.panda__whatpanda-container .panda__features-container__feature-text {
    margin-top: 0.5rem;
    color: white;
}

@media screen and (max-width: 850px) {
    .panda__whatpanda-heading {
        flex-direction: column;
        align-items: flex-start;
        margin-bottom: 0rem;
    }

    .panda__whatpanda-heading p {
        margin-top: 1rem;
    }
}

@media screen and (max-width: 650px) {
    /* Customizing feature component as per needs */
    .panda__whatpanda-feature .panda__features-container__feature {
        flex-direction: column;
    }

    /* Customizing feature component as per needs */
    .panda__whatpanda-feature .panda__features-container_feature-text {
        margin-top: 0.5rem;
    }

    .panda__whatpanda-heading h1 {
        font-size: 28px;
        line-height: 40px;
    }
}

@media screen and (max-width: 350px) {
    /* Customizing feature component as per needs */
    .panda__whatpanda-container .panda__features-container__feature {
        margin: 1rem 0;
        min-width: 100%;
    }
}