.panda__footer{
    color: white;
    display: flex;

    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.panda__footer-heading {
    width: 100%;
    text-align: center;

    margin-bottom: 3rem;
}

.panda__footer-heading h1{
    font-family: Arial;
    font-weight: 800;
    font-size: 42px;
    line-height: 75px;
    text-align: center;
    letter-spacing: -0.04cm;
}
.panda__footer-btn {
    display: flex;
    justify-content: center;
    align-items: center;

    padding: 1rem;
    border: 1px solid white;

    margin-bottom: 5rem;
    cursor: pointer;

}
.panda__footer-btn p{
    box-sizing: border-box;
    font-family: Arial;
    font-size: 18px;
    line-height: 21px;
    color: white;
    word-spacing: 2px;
}

.panda__footer-links{
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    align-items: flex-start;
    flex-direction: row;
    width: 100%;
    text-align: left;
}
.panda__footer-links div{
    width: 230px;
    margin: 1rem;
    
}

.panda__footer-links_logo{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}
.panda__footer-links_logo img{
    width: 150px;
    height: 130px;

    margin-bottom: 1rem;
}

.panda__footer-links_logo p{
    font-family: Arial;
    font-size: 12px;
    line-height: 14px;
}

.panda__footer-links-div{
    display: flex;
    justify-content: flex-start;
    flex-direction: column;
}

.panda__footer-links-div h4{
    font-family: Arial;
    font-size: 14px;
    line-height: 16px;

    margin-bottom: 1rem;

}
.panda__footer-links-div p{
    font-style: 12px;
    line-height: 14px;
    color: white;

    margin: 0.5rem 0;
    cursor: pointer;
}

.panda__footer-copyright {
    margin-top: 2rem;
    text-align: center;
    width: 100%;

}

@media screen and (max-width: 850px) {
    .panda__footer-heading h1{
        font-size: 44px;
        line-height: 50px;
    }
    .panda__footer-links div{
        width: 100%;
        margin: 1rem;
        
    }
}

@media screen and (max-width: 550px) {
    .panda__footer-heading h1{
        font-size: 34px;
        line-height: 42px;
    }

    .panda__footer-links div{
        margin: 1rem 0;

    }
    .panda__footer-btn p{
        font-size: 14px;
        line-height: 20px;
    }
}
@media screen and (max-width: 550px) {
    .panda__footer-heading h1{
        font-size: 27px;
        line-height: 38px;
    }
}