.brand__icons{
    display: flex;
    gap: 25px;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
         /* ff 3.6+ */
    background:-moz-radial-gradient(circle at 0% 57%, rgba(255, 255, 0, 1) 0%, rgba(0, 188, 212, 1) 0%, rgba(130, 140, 238, 1) 100%);

    /* safari 5.1+,chrome 10+ */
    background:-webkit-radial-gradient(circle at 0% 57%, rgba(255, 255, 0, 1) 0%, rgba(0, 188, 212, 1) 0%, rgba(130, 140, 238, 1) 100%);

    /* opera 11.10+ */
    background:-o-radial-gradient(circle at 0% 57%, rgba(255, 255, 0, 1) 0%, rgba(0, 188, 212, 1) 0%, rgba(130, 140, 238, 1) 100%);

    /* ie 10+ */
    background:-ms-radial-gradient(circle at 0% 57%, rgba(255, 255, 0, 1) 0%, rgba(0, 188, 212, 1) 0%, rgba(130, 140, 238, 1) 100%);

    /* global 92%+ browsers support */
    background:radial-gradient(circle at 0% 57%, rgba(255, 255, 0, 1) 0%, rgba(0, 188, 212, 1) 0%, rgba(130, 140, 238, 1) 100%);
}

.facebook,
.twitter,
.instagram,
.google{
    display: flex;
    gap: 5px;
    font-size: 1.5rem;
    cursor: pointer;
    font-family: Arial;
    color: white;

}
