.panda__possibility{
    display: flex;
    flex-direction: row;
    gap: 2rem;

}

.panda__possibility-image{
    flex: 1;
    display: flex;
    justify-content: flex-start;
    align-items: center;


}

.panda__possibility-image img{
    width: 100%;
    height: 100%;
}

.panda__possibility-content{
    flex: 1;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
}

.panda__possibility-content h2{
    color: white;
    font-weight: 900;
    margin: 2rem 0 1rem;
}

.panda__possibility-content p{
    color: white;
    font-size: 16px;
    line-height: 24px;
}

@media screen and (max-width: 950px) {
    .panda__possibility{
        flex-direction: column;
        gap: 2rem;
    }
}