.panda__blog-container_article{
    width: 100%;
    height: 100%;

    display: flex;
    flex-direction: column;
}

.panda__blog-container_article-image{
    width: 100%;
    height: 100%;
}

.panda__blog-container_article-image img{
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.panda__blog-container_article-content{
    display: flex;
    flex-direction: column;
    justify-content: space-between;


    padding: 1rem 1.5rem;
    height: 100%;
}

.panda__blog-container_article-content p{
    font-family: Arial;
    font-weight: bold;
    font-size: 11.649px;
    line-height: 35px;
    color: white;

}


.panda__blog-container_article-content h3{
    font-family: Arial;
    font-weight: 800;
    font-size: 25.11px;
    line-height: 30px;
    color: white;
    cursor: pointer;
    
}

.panda__blog-container_article-content p:last-child{
    cursor: pointer;
    color: red;

}

@media screen and (max-width: 550px) {
    .panda__blog-container_article-content h3{
        font-size: 18px;
        line-height: 25px;
    }
}
