.panda__header{
    display: flex;
}
.panda__header-content {
    flex: 2;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;

}

.panda__header-content h1{
    font-family: Arial, Helvetica, sans-serif;
    font-weight: 800;
    font-size: 50px;
    line-height: 75px;
    letter-spacing: -0.04em;
}

.panda__header-content p{
    font-family:Arial, Helvetica, sans-serif;
    font-style: normal;
    font-weight: normal;
    font-size: 20px;
    line-height: 27px;
    margin: 2rem 0 2rem;


    color: white;

}

.panda__header-content__input{
    width: 100%;
    display: flex;
}

.panda__header-content__input input{
    flex: 2;
    width: 100%;
    min-height: 50px;
    background: pink;
    font-family: Arial;
    font-size: 20px;
    line-height: 27px;
    color: black;
    padding: 0 1rem;
    outline: none;

    border-top-right-radius: 5px ;
    border-bottom-right-radius:  5px;
}

.panda__header-content__input button{
    flex: 0.6;
    width: 100%;
    min-height: 50px;
    background-color: red;
    font-size: 20px;
    line-height: 27px;
    color: white;
    outline: none;
    border: none;
    cursor: pointer;

    border-top-right-radius: 5px ;
    border-bottom-right-radius:  5px;

}
.panda__header-image{
    flex: 1;
    display: flex;
    align-items: center;
    justify-content: center;
}
.panda__header-image img{
    width: 300px;
}

@media screen and (max-width: 1050px) {
    .panda__header{
        flex-direction: column;

    }

    .panda__header-content{
        margin: 0 0 3rem;
    }
}

@media screen and (max-width: 650px) {
    .panda__header h1{
        font-size: 48x;
        line-height: 60px;
    }

    .panda__header p {
        font-size: 16px;
        line-height: 24px;

    }
    .panda__header-content__input input,
    .panda__header-content__input button{
    font-size: 16px;
    line-height: 24px;
    } 

}

@media screen and (max-width: 490px) {
    .panda__header h1{
        font-size: 36x;
        line-height: 48px;
    }

    .panda__header p {
        font-size: 14px;
        line-height: 24px;

    }
    .panda__header-content__input input,
    .panda__header-content__input button{
    font-size: 12px;
    line-height: 16px;
    } 

}