.panda__features{
    width: 100%;
    display: flex;
    justify-content: space-between;
    flex-direction: row;
}

.panda__features-heading{
    flex: 1;
    display: flex;
    justify-content: flex-start;
    align-items: flex-start;
    flex-direction: column;

    text-align: left;
    margin-right: 5rem;
}

.panda__features-heading h1{
    font-family: Arial;
    font-weight: 800;
    font-style: 34px;
    line-height: 45px;
}

.panda__features-container{
    flex: 1.5;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    flex-direction: column;
}

@media screen and (max-width: 990px) {
    .panda__features{
        flex-direction: column;
    }
    
    .panda__features-heading{
        margin: 0 0 2rem 0;
    }
    .panda__features-container__feature{
        flex-direction: column;
    }
}

@media screen and (max-width: 550px) {
    .panda__features-heading h1{
        font-size: 28px;
        line-height: 38px;

    }   
}