.panda__blog{
    display: flex;
    flex-direction: column;
}

.panda__blog-heading {
    width: 100%;
    text-align: left;

    margin-bottom: 5rem;
}

.panda__blog-heading h1{
    font-family: Arial;
    font-style: normal;
    font-size: 42px;
    line-height: 75px;
    letter-spacing: -0.04cm;
}

.panda__blog-container_groupB .panda__blog-container_article-image img{
    height: 250px;
}
.panda__blog-container{
    display: flex;
    flex-direction: row;
    gap: 2rem;


}

.panda__blog-container_groupA{
    flex: 0.75;
    display: flex;
    justify-content: space-between;
    flex-direction: row;
}

.panda__blog-container_groupB{
    flex: 1;

    display: grid;
    grid-template-columns: repeat(2 ,1fr);
    grid-gap: 2rem;
}

@media screen and (max-width: 990px) {
    .panda__blog-container{
        flex-direction: column-reverse;
        gap: 2rem;
    }
    .panda__blog-container-groupA{
        margin: 2rem 0;
    }

    .panda__blog-container-groupA .panda__blog-container_article{
        width: 48%;
    }
    .panda__blog-container-groupA .panda__blog-container_article-image{
        height: 250px;
    }
}

@media screen and  (max-width: 700px) {
    .panda__blog-container_groupB{
        grid-template-columns: repeat(1, 1fr);
    }
    .panda__blog-container-groupA .panda__blog-container_article{
        width: 100%;
    }
    .panda__blog-heading h1 {
        font-size: 46px;
        line-height: 52px;
    }
}
@media screen and (max-width: 550px) {
    .panda__blog-heading h1 {
        font-size: 34px;
        line-height: 42px;
    }
}

