.panda__navbar{
    display: flex;
    justify-content: space-between;
    align-items: center;

    padding: 1rem 6rem;
}

.panda__navbar-links{
    flex: 1;
    display: flex;
    justify-content: flex-start;
    align-items: center;
}

.panda__navbar-links-logo{
    margin-right: 2rem;
}

.panda__navbar-links_container{
    display: flex;
}

.panda__navbar-sign{
    display: flex;
    justify-content: flex-end;
    align-items: center;
}

.panda__navbar-links_container p,
.panda__navbar-sign p,
.panda__navbar-menu_container p{
    color: white;
    font-family: Arial, Helvetica, sans-serif;
    font-weight: 500;
    font-size: 18px;
    line-height: 25px;
    text-transform:capitalize;

    margin: 0 1rem;
    cursor: pointer; 

}

.panda__navbar-sign button,
.panda__navbar-menu_container button{
    padding: 0.5rem 1rem;
    color: white;
    background: red;
    font-family: Arial, Helvetica, sans-serif;
    font-weight: 500;
    font-size: 18px;
    line-height: 25px;
    border-radius: 5px;
    border: none;
    outline: none;
    cursor: pointer;
}

.panda__navbar-menu{
    margin: 5%;
    display: none;
    position: fixed;
    right: 0;
    width: 90vw;
    justify-content: right;

}

.panda__navbar-menu_container{
    display: flex;
    justify-content: flex-start;
    align-items: center;
    flex-direction: column;

    text-align: center;
    background: pink;
    padding: 2rem;
    position: absolute;
    top: 20px;
    right:0 ;
    min-height: 80vh;
    margin-top: 1rem;
    min-width: 100%;
    border-radius: 5px;
    box-shadow: 0 0 5 black;
}

.panda__navbar-menu_container p {
    margin: 1rem 0;
}

.panda__navbar-menu_container-links-sign{
    display: none;
}

@media screen and (max-width: 1050px) {
    .panda__navbar-links_container{
        display: none;
    }

    .panda__navbar-menu{
        display: flex;
    }
    
}

@media screen and (max-width: 700px) {
    .panda__navbar{
        padding: 2rem 4rem;

    }
    
}

@media screen and (max-width: 550px) {
    .panda__navbar{
        padding: 2rem;
        
    }

    .panda__navbar-sign{
        display: none;
    }

    .panda__navbar-menu_container{
        top: 20px;
    }
    .panda__navbar-menu_container-links-sign{
        display: block;
    }
    
}